.app {
  text-align: center;
}

.appMain {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.container {
  display: grid;
  padding-bottom: 2rem;
}


.container input,
.container button {
  width: min(60vw, 20rem);
  font-size: 1.1rem;
  border: 0 solid;
  color: white;
  text-align: center;
  font-family: "Source Code Pro", monospace;
  margin: 0.2rem;
}

.container input {
  padding: 0.5rem 0.1rem;
  margin-bottom: 1rem;
  margin-top: 0.2rem;
  border-radius: 0.4rem;
  background-color: rgba(255, 255, 255, 0.1);
}

.container button {
  padding: 1rem;
  font-weight: bold;
  border-radius: 0.4rem;
  background-color: rgba(255, 255, 255, 0.6);
}

.display {
  border: 0.1rem solid rgba(255, 255, 255, 0.5);
  margin: -1rem 2rem 2rem 2rem;
  padding: 0;
}

.intenseButton {
  padding: 0.5rem 1rem;
}